import React, { Component } from "react";
import PropTypes from "prop-types";
import Button from "../Button";
import styles from "./styles.module.scss"

export default class TelegramLoginButton extends Component {
  componentDidMount() {
    const {
      botName,
      buttonSize,
      cornerRadius,
      requestAccess,
      usePic,
      dataOnauth,
    } = this.props;
    console.log(this.props);
    // console.log(dataOnauth)
    window.TelegramLoginWidget = {
      dataOnauth: user => {
        console.log("user: " + user);
        dataOnauth(user);
      },
    };

    const script = document.createElement("script");
    script.src = "https://telegram.org/js/telegram-widget.js?4";
    script.setAttribute("data-telegram-login", botName);
    script.setAttribute("data-size", buttonSize);
    if (cornerRadius !== undefined) {
      script.setAttribute("data-radius", cornerRadius);
    }
    script.setAttribute("data-request-access", requestAccess);
    script.setAttribute("data-userpic", usePic);
    script.setAttribute("data-onauth", "TelegramLoginWidget.dataOnauth(user)");
    script.async = true;

    // let src = "https://telegram.org/js/telegram-widget.js?4";
    // let script = `script async src=${src} data-telegram-login=${botName} data-size=${buttonSize} data-radius=${cornerRadius}
    //  data-request-access=${requestAccess} data-userpic=false data-onauth=TelegramLoginWidget.dataOnauth(user)`;
    // document.getElementsByTagName("body")[0].appendChild(script);
    this.instance.appendChild(script);
  }

  render() {
    const text = "Login via Telegram";

    return (
      <div
        // style={{ visibility: "hidden" }}
        // className={this.props.className}
        className={styles.telegram__iframe__block}
        ref={component => {
          console.log("this.props.className: " + this.props.className);
          console.log("component: " + component);
          console.log("this.instance: " + this.instance);
          this.instance = component;
        }}
      >
        {console.log("this.props.children: " + this.props.children)}
        {/*{this.props.children}*/}
        <Button text={text}/>
      </div>
    );
  }
}

